import React, { useEffect, useRef, useState } from "react";
import Chart from "chart.js/auto";
/* import HourlyTable from "./WeatherTableRain"; */

function WeatherGraphTemp({ hourly, hourlyunits, horaActual }) {
  const chartRef = useRef(null);
  const chartInstance = useRef(null);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [windowHeight, setWindowHeight] = useState(window.innerHeight / 5);

  useEffect(() => {
    if (hourly.time.length === 0) return;

    const labels = hourly.time
      .map((hora) => hora.slice(-5, -3))
      .slice(horaActual, horaActual + 24);
    const temperaturaData = hourly.temperature_2m.slice(
      horaActual,
      horaActual + 24
    );

    const ctx = chartRef.current.getContext("2d");

    // Destruye el grfico anterior si existe
    if (chartInstance.current) {
      chartInstance.current.destroy();
    }

    chartInstance.current = new Chart(ctx, {
      type: "line",
      data: {
        labels,
        datasets: [
          {
            label: "Temperatura (" + hourlyunits.temperature_2m + ")",
            data: temperaturaData,
            backgroundColor: "rgba(75, 192, 192, 0.8)",
            borderColor: "rgba(75, 192, 192, 1)",
            borderWidth: 2,
            tension: 0.3,
            yAxisID: "temperatura-y-axis",
            display: false,
          },
        ],
      },
      options: {
        scales: {
          x: {
            beginAtZero: true,
          },
          "temperatura-y-axis": {
            type: "linear",
            position: "left",
            title: {
              display: true,
              text: "Temperatura (" + hourlyunits.temperature_2m + ")", // Ttulo del eje Y
            },
            ticks: {
              callback: (value) => `${value}C`, // Agrega "C" al valor
            },
          },
        },
        plugins: {
          title: {
            display: true,
            text: "Grfico de Temperatura", // Ttulo del grfico
          },
          legend: {
            display: false, // Oculta el legend (label) del grfico
          },
        },
      },
    });

    // Agrega un listener para detectar cambios en el tamao de la ventana
    window.addEventListener("resize", handleWindowResize);

    return () => {
      // Elimina el listener cuando el componente se desmonta
      window.removeEventListener("resize", handleWindowResize);
    };
  }, [
    windowWidth,
    windowHeight,
    horaActual,
    hourly.temperature_2m,
    hourly.time,
    hourlyunits.temperature_2m,
  ]);

  const handleWindowResize = () => {
    // Actualiza el estado del ancho de la ventana al cambiar el tamao
    setWindowWidth(window.innerWidth);
    setWindowHeight(window.innerHeight);
  };

  return (
    <div>
      <canvas ref={chartRef} width={windowWidth} height={windowHeight} />
      {/*       <HourlyTable
        hourlyData={hourly.time
          .slice(horaActual, horaActual + 24)
          .map((hour, index) => ({
            hour: hour.slice(-5, -3),
            rain: hourly.precipitation_probability[horaActual + index],
          }))}
      /> */}
    </div>
  );
}

export default WeatherGraphTemp;
